import { Component, OnInit,HostListener } from '@angular/core';
declare var $: any;
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.less']
})
export class AppComponent {

  constructor() {

  }
  ngOnInit() {
    var tabsContainer = $(".screen-tabs-container");
    var windowWidth = window.innerWidth;
    var lastLink = $(".screen-tabs-container .list-item:last-child .link");
    var firstLink = $(".screen-tabs-container .list-item:first-child .link")
    var tabs = $(".screen-tabs-container .list-item .link");
    tabs.click(function (event) { event.preventDefault(); })
    if (windowWidth > 700) {
      lastLink.click(function (event) {
        var offset = $(".tabs-list").width() - tabsContainer.width();
        event.preventDefault();
        tabsContainer.animate({ scrollLeft: offset }, 800)
      }
      )
      firstLink.click(function (event) {
        var offset = $(".tabs-list").width() - tabsContainer.width();
        event.preventDefault();
        tabsContainer.animate({ scrollLeft: -offset }, 800)
      }
      )
    }else{
      lastLink.click(function (event) {
        var offset = $(".tabs-list--mobile").width() - tabsContainer.width()+50;
        event.preventDefault();
        tabsContainer.animate({ scrollLeft: offset }, 800)
      }
      )
      firstLink.click(function (event) {
        var offset = $(".tabs-list--mobile").width() - tabsContainer.width();
        event.preventDefault();
        tabsContainer.animate({ scrollLeft: -offset }, 800)
      }
      )
    }

    $(".main-header--mobile .icon-menu").click(function () {
      $('.main-header--mobile .header__menu-container .header-menu').toggleClass('active')


    })

  }




}

