import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'welcome-page-start',
  templateUrl: './welcome-page-start.component.html',
  styleUrls: ['./welcome-page-start.component.less']
})
export class WelcomePageStartComponent implements OnInit {
  isValid: boolean = true;
  model: mainForm = {
    name: '',
    phone: '',
    restorant: '',
    email: '',
  };

  enablePolicy = false;

  constructor() { }

  ngOnInit() {
  }

  send() {
    if (!this.enablePolicy) {
      return false;
    }

    this.validate()

    return false;
  }

  validate() {

    this.validateEmail();
    if (this.model.name === '' || this.model.phone === '' || this.model.restorant === '' || this.model.email === '') {
      this.isValid = false;
    }


  }

  validateEmail(): boolean {
    const mailPattern = /(([a-zA-Z0-9\-\_\.])+@[a-zA-Z\_]+?(\.[a-zA-Z]{2,6})+)/gim;
    const isValid = mailPattern.test(this.model.email);
    if (!isValid) {
      this.isValid = false;
    }

    return isValid;
  }



}

export interface mainForm {
  name: string;
  phone: string;
  email: string;
  restorant: string;
}
