import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { WelcomePageActivateComponent } from './components/pages/welcome-page-activate/welcome-page-activate.component';
import { WelcomePageBuyComponent } from './components/pages/welcome-page-buy/welcome-page-buy.component';
import {WelcomePageStartComponent} from './components/pages/welcome-page-start/welcome-page-start.component';

const routes: Routes = [
  {
    path: '', component: WelcomePageStartComponent
  },
  {
    path: 'welcome-page-activate', component: WelcomePageActivateComponent
  }, {
    path: 'welcome-page-start', redirectTo: '', pathMatch: 'full'
  },{
    path: 'welcome-page-buy', component: WelcomePageBuyComponent
  }
];


@NgModule({
  imports: [
    RouterModule.forRoot(routes)
  ],
  exports: [RouterModule]
})
export class AppRouterModule { }
